<template>
  <div>
    <content-title
      title="邮件过滤系统(部门)"
      tips="屏蔽简历管理、岗位关键字及岗位管理。关键字导入请使用关键字导出的表格格式，和文件格式（.xls）">
      <el-select v-model="value" placeholder="请选择部门" class="sel-dep" @change="selectDepartment"
                 v-if="user.roles.some(function(role) {
           return role.id === rolesCpt.admin
           })">
        <el-option
          v-for="item in departmentList"
          :key="item.id"
          :label="item.name"
          :value="item.id"
          v-if="item.id !== adminDepartmentIdCpt">
        </el-option>
      </el-select>
    </content-title>
    <nav-bar :nav="nav" @navBack="navBack">
      <!--<el-input-->
      <!--class="com-search"-->
      <!--placeholder="用户名/ID"-->
      <!--v-model="searchVal">-->
      <!--<i slot="prefix" class="el-input__icon el-icon-search"></i>-->
      <!--</el-input>-->
    </nav-bar>
    <content-c>
      <component :is="currentView"></component>
    </content-c>
  </div>
</template>
<script>
  import contentTitle from '../../componentsBody/contentTitle.vue'
  import contentC from '../../componentsBody/contentC.vue'
  import navBar from '../../componentsBody/navBar.vue'

  import depKeywords from './posKeywords/poskeywords.vue'
  import depManage from './posManage/posManage.vue'
  import screenResume from './screenResume/screenResume.vue'

  import { mapState, mapMutations, mapActions } from 'vuex'
  import { roles, adminDepartmentId } from '../../../utils/baseConfig'

  export default {
    name: '',
    props: {},
    data () {
      return {
        nav: [
          {name: '屏蔽简历管理'},
          {name: '岗位专用关键字'},
          {name: '岗位管理'},
        ],
        searchVal: '',
        currentView: screenResume,
        options: [{
          value: '1',
          label: 'android'
        }, {
          value: '2',
          label: 'IOS'
        }, {
          value: '3',
          label: '前端'
        }, {
          value: '4',
          label: '后端'
        }, {
          value: '5',
          label: 'ui/ue'
        }],
        value: '',
      }
    },
    computed: {
      ...mapState([
        'user'
      ]),
      ...mapState('department', [
        'departmentList',
      ]),
      ...mapState([
        'departmentId'
      ]),
      rolesCpt () {
        return roles
      },
      adminDepartmentIdCpt () {
        return adminDepartmentId
      }
    },
    methods: {
      ...mapActions('department', [
        'ac_departmentList'
      ]),
      ...mapMutations([
        'mut_departmentId',
      ]),
      navBack (menu) {
        switch (menu.index) {
          case 0:
            this.currentView = screenResume
            break
          case 1:
            this.currentView = depKeywords
            break
          case 2:
            this.currentView = depManage
            break
          default:
            this.currentView = screenResume
        }
      },
      handleSizeChange () {},
      handleCurrentChange () {},
      selectDepartment () {
        this.mut_departmentId(this.value)
      }
    },
    components: {
      contentTitle,
      contentC,
      navBar,
      depKeywords,
      depManage,
      screenResume,
    },
    beforeCreate () {},
    created () {
      this.ac_departmentList()
    },
    beforeMount () {},
    mounted () {
      if (this.departmentId) {
        this.value = this.departmentId
      }
    },
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../styles/common";

  .sel-dep {
    width: 180px;
    float: right;
    margin-right: 10px;
    margin-top: -45px;
  }
</style>



