<template>
  <div>
    <el-table
      :data="resumeList"
      v-if="departmentId"
      style="width: 100%">
      <el-table-column
        prop="username"
        label="姓名">
      </el-table-column>
      <el-table-column
        prop="job.department.name"
        label="投递部门">
      </el-table-column>
      <el-table-column
        prop="form.value"
        label="来源">
      </el-table-column>
      <el-table-column
        prop="id"
        label="ID">
      </el-table-column>
      <el-table-column
        prop="operationUser.name"
        label="操作人员">
      </el-table-column>
      <el-table-column
        prop="operationTime"
        label="处理时间">
        <template slot-scope="scope">
          {{ getTimeFormat(scope.row.operationTime) }}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        width="100">
        <template slot-scope="scope">
          <button class="com-btn com-detail" @click="handleDetail(scope.row, scope.column, scope.$index)">查看</button>
        </template>
      </el-table-column>
    </el-table>

    <div class="com-page-box">
      <div class="com-pages">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="resumeListTotal" v-if="departmentId">
        </el-pagination>
      </div>
    </div>

    <div v-if="!departmentId" style="padding: 30px 50px;text-align: center;border-bottom: 1px dashed #ccc;color: #888;">
      请在右上角选择部门
    </div>

    <!--查看-->
    <com-dialog
      :show="detailDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="resume-detail">
        <p><span class="label">姓&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;名:</span>{{resumeData.name}}</p>
        <p><span class="label">投递部门:</span>{{resumeData.departmentName}}</p>
        <p><span class="label">来&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;源:</span>{{resumeData.from}}</p>
        <p><span
          class="label">I&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;D:</span>{{resumeData.id}}
        </p>
        <p><span class="label">操作人员:</span>{{resumeData.opUser}}</p>
        <p><span class="label">处理时间:</span>{{resumeData.processedTime}}</p>
        <p><span class="label">电话号码:</span>{{resumeData.phone}}</p>
        <p><span class="label">屏蔽原因:</span>{{resumeData.remarks}}</p>
      </div>

      <div slot="title" class="com-dialog-title t-1">
        查看信息
        <!--<div class="close x-color-1" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #64A5E7; border-color: #64A5E7;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>
  </div>
</template>
<script>
  import comDialog from '../../../../components/toast/dialog.vue'
  import { formatDateTime } from '../../../../utils/utils'
  import { mapState, mapActions } from 'vuex'

  export default {
    name: '',
    props: {},
    data () {
      return {
        currentPage: 1,
        pageSize: 10, // 每页条数
        pageSizes: [10, 20],
        resumeData: {
          name: '',
          departmentName: '',
          from: '',
          id: '',
          opUser: '',
          processedTime: ''
        },
        detailDialogVisible: false,
        currentStatusId: 2, // 屏蔽
      }
    },
    computed: {
      ...mapState('resume', [
        'resumeList',
        'resumeListTotal',
      ]),
      ...mapState([
        'departmentId'
      ])
    },
    watch: {
      departmentId () {
        this.ac_resumeList({
          entity: {
            statusBig: {id: this.currentStatusId},
            job: {department: {id: this.departmentId}}
          },
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      }
    },
    methods: {
      ...mapActions('resume', [
        'ac_resumeList'
      ]),
      handleDetail (row) {
        this.detailDialogVisible = true
        this.resumeData = {
          name: row.username,
          departmentName: row.job ? row.job.department.name : '',
          from: row.form ? row.form.value : '',
          id: row.id,
          opUser: row.operationUser.name,
          processedTime: this.getTimeFormat(row.operationTime),
          remarks: row.remarks,
          phone: row.phone
        }
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.ac_resumeList({
          entity: {
            statusBig: {id: this.currentStatusId},
            job: {department: {id: this.departmentId}}
          },
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.ac_resumeList({
          entity: {
            statusBig: {id: this.currentStatusId},
            job: {department: {id: this.departmentId}}
          },
          pageNo: page - 1,
          pageSize: this.pageSize
        })
      },
      closeDialog () {
        this.detailDialogVisible = false
      },
      getTimeFormat (timeStamp) {
        return formatDateTime(timeStamp)
      },
    },
    components: {
      comDialog,
    },
    beforeCreate () {},
    created () {
      if (this.departmentId) {
        this.ac_resumeList({
          entity: {
            statusBig: {id: this.currentStatusId},
            job: {department: {id: this.departmentId}}
          },
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      }
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../../styles/common";

  .t-1 {
    background-color: #64A5E7;
  }

  .x-color-1 {
    color: #64A5E7;
  }

  .resume-detail {
    p {
      line-height: 40px;
    }
    .label {
      margin-right: 20px;
      &.label-block {
        display: block;
      }
    }
  }
</style>



