<template>
  <div>
    <div class="select-pos">
      <label>请选择需要设置的岗位: </label>
      <el-select v-model="posValue"
                 placeholder="请先选择部门后选择岗位"
                 class="sel-pos"
                 :disabled="selectPosDisabled"
                 @change="selectPos">
        <el-option
          v-for="item in posList"
          :key="item.id"
          :label="item.name"
          :value="item.id">
        </el-option>
      </el-select>
    </div>
    <div class="add-keywords">
      <div class="add" @click="addKeyword">+关键字</div>
      <div class="import">导入<input type="file" accept="application/vnd.ms-excel" name="xls" class="import-file"
                                   @change="importFile($event)"></div>
      <div class="export" @click="exportFile">导出</div>
    </div>
    <ul class="dep-box">
      <li v-for="keyword in keywordsList">
        <span>{{keyword.integral}}分</span>&nbsp;&nbsp;-&nbsp;&nbsp;<span>{{keyword.value}}</span>
        <i class="sprites icon-del" @click="delKeywords(keyword)"></i>
        <i class="sprites icon-edit" @click="editKeywords(keyword)"></i></li>
    </ul>
    <div class="com-page-box">
      <div class="com-pages">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="keywordsListTotal"
          v-if="keywordsListTotal > 10">
        </el-pagination>
      </div>
    </div>

    <!--新增关键字-->
    <com-dialog :show="addKeywordsDialogVisible"
                title="标题"
                :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <el-form :model="formData"
               :rules="rules"
               ref="formAddRef1"
               label-width="100px"
               class="demo-ruleForm"
               label-position="top"
      >

        <el-form-item label="分值(可为负)" prop="integral">
          <el-input-number style="width: 40%" v-model="formData.integral" controls-position="right" :min="-100"
                           :max="100"></el-input-number>
        </el-form-item>

        <el-form-item label="关键字" prop="value">
          <el-input v-model="formData.value"></el-input>
        </el-form-item>
      </el-form>
      <div slot="title" class="com-dialog-title t-1">
        新增关键词
        <!--<div class="close x-color-1" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #64A5E7; border-color: #64A5E7;" type="primary"
                   @click="handleAddKeywordsSave('formAddRef1')">保存
        </el-button>
      </div>
    </com-dialog>

    <!--编辑关键字-->
    <com-dialog :show="editKeywordsDialogVisible"
                title="标题"
                :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <el-form :model="formData"
               :rules="rules"
               ref="formAddRef2"
               label-width="100px"
               class="demo-ruleForm"
               label-position="top"
      >

        <el-form-item label="分值(可为负)" prop="integral">
          <el-input-number style="width: 40%" v-model="formData.integral" controls-position="right" :min="-100"
                           :max="100"></el-input-number>
        </el-form-item>

        <el-form-item label="关键字" prop="value">
          <el-input v-model="formData.value"></el-input>
        </el-form-item>
      </el-form>
      <div slot="title" class="com-dialog-title t-2">
        编辑关键词
        <!--<div class="close x-color-2" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #000000; border-color: #000000;" type="primary"
                   @click="handleAddKeywordsSave('formAddRef2')">保存
        </el-button>
      </div>
    </com-dialog>
  </div>
</template>
<script>
  import comDialog from '../../../../components/toast/dialog.vue'
  import { mapState, mapActions, mapMutations } from 'vuex'
  import { serverIp } from '../../../../utils/baseConfig'

  export default {
    name: '',
    props: {},
    data () {
      return {
        addKeywordsDialogVisible: false,
        editKeywordsDialogVisible: false,
        formData: {
          integral: 0,
          value: ''
        },
        rules: {
//          integral: [
//            {required: true, message: '请输入分值'},
//            {min: -100, max: 100, message: '分值在-100到100之间'}
//          ],
          value: [
            {required: true, message: '请输入关键字', trigger: 'blur'},
            {min: 1, message: '关键字最短长度为1', trigger: 'blur'}
          ]
        },
        currentPos: {},
        posValue: '',
        selectPosDisabled: true,
        currentPage: 1, // 当前页
        pageSize: 20, // 每页条数
        pageSizes: [10, 20, 30],
      }
    },
    computed: {
      ...mapState('keywords', [
        'keywordsList',
        'addKeywordsStatus',
        'delKeywordsStatus',
        'keywordsListTotal',
        'importStatus',
      ]),
      ...mapState([
        'departmentId'
      ]),
      ...mapState('position', [
        'posList',
      ])
    },
    watch: {
      departmentId (d) {
        this.ac_posList({
          entity: {department: {id: this.departmentId}},
          pageNo: 0,
          pageSize: 10
        }).then(() => {
          this.selectPosDisabled = false
        })
      },
      addKeywordsStatus (d) {
        if (d.status) {
          this.ac_keywordsList({
            entity: {job: {id: this.posValue}},
            pageNo: 0,
            pageSize: 10000
          }).then(() => {
            this.closeDialog()
          })
        }
      },
      delKeywordsStatus (d) {
        if (d.status) {
          this.ac_keywordsList({
            entity: {job: {id: this.posValue}},
            pageNo: 0,
            pageSize: 10000
          }).then(() => {
            this.$message({
              type: 'success',
              message: d.statusMsg
            })
          })
        }
      },
      importStatus (d) {
        if (d.status === 1) {
          if (this.departmentId) {
            this.ac_posList({
              entity: {department: {id: this.departmentId}},
              pageNo: 0,
              pageSize: 10
            }).then(() => {
              this.$message({
                type: 'success',
                message: d.msg
              })
            })
          }
        } else if (d.status === 2) {
          this.$message({
            type: 'error',
            message: d.msg
          })
        }
      }
    },
    methods: {
      ...mapActions('keywords', [
        'ac_keywordsList',
        'ac_addAndModifyKeywords',
        'ac_delKeywords',
        'ac_keywordsExport',
        'ac_keywordsImport',
      ]),
      ...mapActions('position', [
        'ac_posList',
      ]),
      ...mapMutations('keywords', [
        'mut_keywordsList',
        'mut_keywordsListTotal',
      ]),
      handleAddKeywordsSave (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let param = {
              integral: this.formData.integral,
              value: this.formData.value,
              job: {id: this.posValue}
            }
            if (this.formData.id) { // 区分添加和修改
              param.id = this.formData.id
            }
            this.ac_addAndModifyKeywords(param)
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      closeDialog () {
        this.addKeywordsDialogVisible = false
        this.editKeywordsDialogVisible = false
        this.formData = {
          integral: 0,
          value: ''
        }
      },
      addKeyword () {
        if (this.posValue) {
          this.addKeywordsDialogVisible = true
        } else {
          this.$message('请先选择岗位')
        }
      },
      editKeywords (key) {
        if (this.posValue) {
          this.formData = {
            id: key.id,
            integral: key.integral,
            value: key.value
          }
          this.editKeywordsDialogVisible = true
        } else {
          this.$message('请先选择岗位')
        }
      },
      delKeywords (key) {
        this.$confirm('确定删除该关键字, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.ac_delKeywords({
            resumeKeyId: key.id
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      },
      selectPos () {
        this.ac_keywordsList({
          entity: {job: {id: this.posValue}},
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.selectPos(this.currentPos)
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.selectPos(this.currentPos)
      },
      importFile (e) {
        let files = e.target.files || e.dataTransfer.files
//         实例化一个表单数据对象
        let formData = new FormData()
        formData.append('filename', files[0].name)
        formData.append('xls', files[0])
        this.ac_keywordsImport(formData)
      },
      exportFile () {
        if (this.currentPos.id !== -1) {
          window.location.href = serverIp + '/oa/resume/key/' + this.currentPos.id + '/export'
        } else {
          window.location.href = serverIp + '/oa/resume/key/0/export'
        }
      }
    },
    components: {
      comDialog,
    },
    beforeCreate () {},
    created () {
      this.mut_keywordsList([])
      this.mut_keywordsListTotal([])
      if (this.departmentId) {
        this.ac_posList({
          entity: {department: {id: this.departmentId}},
          pageNo: 0,
          pageSize: 10
        }).then(() => {
          this.selectPosDisabled = false
        })
      }
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../../styles/common";

  $li-w: 400px;
  $add-w: 200px;

  .select-pos {
    margin-left: 20px;
    margin-bottom: 10px;
  }

  .add-keywords {
    width: $li-w;
    display: flex;
    font-size: 12px;
    margin-left: 20px;
    .add {
      color: #fff;
      border-radius: 3px;
      width: $add-w;
      margin-right: 10px;
      background-color: #64A5E7;
      padding: 3px 0;
      text-align: center;
      cursor: pointer;
    }
    .import {
      color: #fff;
      border-radius: 3px;
      width: ($li-w - $add-w -10px)/2;
      margin-right: 10px;
      background-color: #FEC71E;
      padding: 3px 0;
      text-align: center;
      cursor: pointer;
      position: relative;
      overflow: hidden;
      .import-file {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        opacity: 0;
      }
    }
    .export {
      color: #fff;
      border-radius: 3px;
      width: ($li-w - $add-w -10px)/2;
      background-color: #5AD1D1;
      padding: 3px 0;
      text-align: center;
      cursor: pointer;
    }
  }

  .dep-box {
    display: flex;
    flex-wrap: wrap;
    li {
      width: $li-w;
      padding: 5px 10px;
      border: 1px solid #E1E1E1;
      background-color: #EFEFEF;
      font-size: 12px;
      margin: 14px 20px;
      border-radius: 5px;
      color: #42454A;
      box-sizing: border-box;
      i {
        float: right;
        margin-left: 20px;
        cursor: pointer;
      }
      &:after {
        content: '';
        display: inline-block;
        clear: both;
      }
    }
  }

  .t-1 {
    background-color: #64A5E7;
  }

  .x-color-1 {
    color: #64A5E7;
  }

  .t-2 {
    background-color: #000000;
  }

  .x-color-2 {
    color: #000000;
  }
</style>



