<template>
  <div>
    <el-table
      :data="posList"
      v-if="departmentId"
      style="width: 100%">
      <el-table-column
        prop="name"
        label="岗位名称">
      </el-table-column>
      <el-table-column
        prop="department.name"
        label="所属部门">
      </el-table-column>
      <el-table-column
        prop="key"
        label="邮件标题关键字">
      </el-table-column>
      <el-table-column
        prop="mailSize"
        label="大小阈值(kb)">
      </el-table-column>
      <el-table-column
        prop="integral"
        label="积分阈值(分)">
      </el-table-column>

      <el-table-column
        label="操作"
        width="200"
        v-if="user.roles.some(function(role) {
            return role.id === rolesCpt.admin || role.id === rolesCpt.principal
          })">
        <template slot-scope="scope">
          <button class="com-btn com-detail" @click="handleDetail(scope.row, scope.column, scope.$index)">查看</button>
          <button class="com-btn com-edit" @click="handleEdit(scope.row, scope.column, scope.$index)">编辑</button>
        </template>
      </el-table-column>
    </el-table>

    <div class="com-page-box">
      <div class="com-pages">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalElements" v-if="totalElements > 10">
        </el-pagination>
      </div>
    </div>

    <div v-if="!departmentId" style="padding: 30px 50px;text-align: center;border-bottom: 1px dashed #ccc;color: #888;">
      请在右上角选择部门
    </div>

    <!--查看-->
    <com-dialog
      :show="detailDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="resume-detail">
        <p><span class="label">岗位名称:</span>{{posData.name}}</p>
        <p><span class="label">所属部门:</span>{{posData.department.name}}</p>
        <p><span class="label">邮件标题关键字:</span>{{posData.key}}</p>
        <p><span class="label">积&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;分:</span>{{posData.integral}} 分</p>
        <p><span class="label">大&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;小:</span>{{posData.mailSize}} kb</p>
      </div>

      <div slot="title" class="com-dialog-title t-1">
        查看信息
        <!--<div class="close x-color-1" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #64A5E7; border-color: #64A5E7;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>

    <!--编辑-->
    <com-dialog
      :show="editDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <el-form :model="posEditForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-row>
          <el-col :span="12">
            <el-form-item label="积分阈值" prop="in">
              <el-input v-model="posEditForm.in"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6" style="padding-top: 3px;padding-left: 2px;color: #7E7C79;">
            分
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="12">
            <el-form-item label="大小阈值" prop="size">
              <el-input v-model="posEditForm.size"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6" style="padding-top: 8px;padding-left: 2px;color: #7E7C79;">
            kb
          </el-col>
        </el-row>
      </el-form>

      <div slot="title" class="com-dialog-title t-2">
        编辑岗位
        <!--<div class="close x-color-2" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #FEC71E; border-color: #FEC71E;" type="primary"
                   @click="saveEditPos('ruleForm')">确定
        </el-button>
      </div>
    </com-dialog>
  </div>
</template>
<script>
  import { mapState, mapActions } from 'vuex'
  import { formatDateTime } from '../../../../utils/utils'
  import comDialog from '../../../../components/toast/dialog.vue'
  import { roles } from '../../../../utils/baseConfig'

  export default {
    name: '',
    props: {},
    data () {
      return {
        currentPage: 1,
        pageSize: 10, // 每页条数
        pageSizes: [10, 20],
        detailDialogVisible: false,
        editDialogVisible: false,
        posData: {
          department: {}
        },
        posEditForm: {
          size: 0,
          in: 0
        },
        rules: {
          size: [
            {required: true, message: '请输入大小'}
          ],
          in: [
            {required: true, message: '请输入积分'}
          ]
        }
      }
    },
    computed: {
      ...mapState('position', [
        'posList',
        'totalElements',
        'addPosStatus',
      ]),
      ...mapState([
        'departmentId',
      ]),
      ...mapState([
        'user'
      ]),
      rolesCpt () {
        return roles
      }
    },
    watch: {
      departmentId () {
        this.ac_posList({
          entity: {department: {id: this.departmentId}},
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      addPosStatus (d) {
        if (d) {
          this.ac_posList({
            entity: {department: {id: this.departmentId}},
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          }).then(() => {
            this.closeDialog()
          })
        }
      }
    },
    methods: {
      ...mapActions('position', [
        'ac_posList',
        'ac_posAddAndModify',
      ]),

      handleDetail (row) {
        this.detailDialogVisible = true
        this.posData = row
      },
      handleEdit (row) {
        this.editDialogVisible = true
        this.posData = row
        this.posEditForm = {
          size: row.mailSize,
          in: row.integral
        }
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.ac_posList({
          entity: {department: {id: this.departmentId}},
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.ac_posList({
          entity: {department: {id: this.departmentId}},
          pageNo: page - 1,
          pageSize: this.pageSize
        })
      },
      cptFormatDateTime (Timestamp) {
        return formatDateTime(Timestamp)
      },
      closeDialog () {
        this.detailDialogVisible = false
        this.editDialogVisible = false
        this.posData = {
          department: {}
        }
      },
      saveEditPos (formName) {
        this.integralRule()
        this.mailRule()
        this.$nextTick(() => {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              this.ac_posAddAndModify({
                id: this.posData.id,
                name: this.posData.name,
                key: this.posData.key,
                department: {
                  id: this.posData.department.id,
                },
                mailSize: this.posEditForm.size,
                integral: this.posEditForm.in,
              })
            } else {
              console.log('error submit!!')
              return false
            }
          })
        })
      },
      integralRule () {
        if (!(/(^[0-9]\d*$)/.test(this.posEditForm.in))) {
          this.posEditForm.in = ''
        }
      },
      mailRule () {
        if (!(/(^[0-9]\d*$)/.test(this.posEditForm.size))) {
          this.posEditForm.size = ''
        }
      }
    },
    components: {
      comDialog,
    },
    beforeCreate () {},
    created () {
      if (this.departmentId) {
        this.ac_posList({
          entity: {department: {id: this.departmentId}},
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      }
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../../styles/common";

  .t-1 {
    background-color: #64A5E7;
  }

  .x-color-1 {
    color: #64A5E7;
  }

  .t-2 {
    background-color: #FDC61E;
  }

  .x-color-2 {
    color: #FDC61E;
  }

  .resume-detail {
    p {
      line-height: 40px;
    }
    .label {
      margin-right: 20px;
      &.label-block {
        display: block;
      }
    }
  }
</style>



